@charset "UTF-8";
.product-detailed {
  --v-product-detailde-padding: 1.3333rem;
  --v-detailed-slider-img-proc-size: 100%;
  --v-detailed-slider-border-radius: 4px;
}

.detailed-slider {
  --v-detailed-slider-content-margin-bottom: 2rem;
}
.detailed-slider__content {
  position: relative;
  overflow: hidden;
  margin-bottom: var(--v-detailed-slider-content-margin-bottom);
  border-radius: var(--v-detailed-slider-border-radius);
}
.detailed-slider__item {
  height: 100%;
}
.detailed-slider__img {
  max-height: 570px;
}
.detailed-slider__img img {
  height: var(--v-detailed-slider-img-proc-size);
  width: var(--v-detailed-slider-img-proc-size);
  object-fit: cover;
}

.detailed-thumbs__content {
  overflow: hidden;
  height: 70px;
}
.detailed-thumbs__img {
  height: 100%;
  overflow: hidden;
  cursor: pointer;
  border-radius: var(--v-detailed-slider-border-radius);
}
.detailed-thumbs__img img {
  height: var(--v-detailed-slider-img-proc-size);
  width: var(--v-detailed-slider-img-proc-size);
  object-fit: cover;
}
.detailed-thumbs .swiper-slide-thumb-active {
  border: 2px solid var(--v-danger);
  border-radius: 6px;
}

.detailed-parameters {
  --v-detailed-parameters-gap: 2rem;
  height: 100%;
}
.detailed-parameters__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  gap: var(--v-detailed-parameters-gap);
}

.palette {
  --v-palette-gap: 1.6667rem;
  --v-palette-list-gap: 0.5333rem;
  --v-palette-colors-size: 30px;
  --v-palette-colors-border-radius: 100%;
  display: flex;
  flex-direction: column;
  gap: var(--v-palette-gap);
  padding: var(--v-product-detailde-padding);
  background-color: var(--v-black);
  /* УЗНАТЬ ПО ПОВОДУ ЦВЕТОВ КАК ЛУЧШЕ СДЕЛАТЬ ДЛЯ КОНТЕНТ МЕНЕДЖЕРА */
}
.palette__list {
  display: flex;
  flex-wrap: wrap;
  gap: var(--v-palette-list-gap);
  align-items: center;
}
.palette__item {
  cursor: pointer;
  /* Вариант, но не попадает в габариты */
  /* Вариант который не точ в точ повторяет сам круг */
}
.palette__item--active {
  position: relative;
}
.palette__item--active::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: var(--v-palette-colors-size);
  width: var(--v-palette-colors-size);
  border-radius: var(--v-palette-colors-border-radius);
  border: 2px var(--v-danger) solid;
  opacity: 0;
  animation: appear 0.25s forwards;
}
.palette__item--disabled {
  position: relative;
  border: 1px var(--v-gray-700) solid;
  border-radius: var(--v-palette-colors-border-radius);
  pointer-events: none;
}
.palette__item--disabled .palette__colors {
  filter: brightness(0.4);
}
.palette__item--disabled::before {
  content: "";
  position: absolute;
  width: var(--v-palette-colors-size);
  background-color: var(--v-gray-700);
  top: 50%;
  transform: rotate(-45deg);
  z-index: 2;
  height: 1px;
}
.palette__colors {
  background: center center/cover no-repeat;
  width: var(--v-palette-colors-size);
  height: var(--v-palette-colors-size);
  border-radius: var(--v-palette-colors-border-radius);
  border: 5px var(--v-dark) solid;
}

.detailed-characteristics {
  --v-detailed-characteristics-item-padding-y: 0.6667rem;
  --v-detailed-characteristics-gap: 1.3333rem;
  display: flex;
  flex-direction: column;
  gap: var(--v-detailed-characteristics-gap);
  padding: 10px var(--v-product-detailde-padding) var(--v-product-detailde-padding) var(--v-product-detailde-padding);
  background-color: var(--v-black);
}
.detailed-characteristics__list {
  padding: 0;
  margin: 0;
}
.detailed-characteristics__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--v-detailed-characteristics-item-padding-y) 0;
  border-bottom: 1px var(--v-gray-700) solid;
}

.detailed-discount {
  --v-detailed-discount-gap: 0.6667rem;
  display: flex;
  padding: var(--v-product-detailde-padding);
  flex-direction: column;
  text-align: center;
  gap: var(--v-detailed-discount-gap);
  background-color: var(--v-black);
}
.detailed-discount__title {
  line-height: 120%;
}
.detailed-discount__text {
  line-height: 120%;
}
.detailed-discount__icon {
  font-size: 1.3333rem;
  color: var(--v-lemon);
  /* МОжно убрать */
}
.detailed-discount__icon * {
  height: 20px;
}

.detailed-purchase {
  --v-detailed-purchase-container-gap: 2rem;
  --v-detailed-purchase-buy-gap: 1.3333rem;
  --v-detailed-purchase-container-padding: 1.3333rem;
  --v-detailed-purchase-delivery-item-padding: 0.6667rem;
  height: 100%;
}
.detailed-purchase__container {
  display: flex;
  flex-direction: column;
  gap: var(--v-detailed-purchase-container-gap);
  height: 100%;
  background-color: var(--v-black);
  padding: var(--v-detailed-purchase-container-padding);
}
.detailed-purchase__price-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.detailed-purchase__prices {
  display: flex;
  align-items: end;
  flex-wrap: wrap;
  gap: 0.6667rem;
}
.detailed-purchase__price {
  line-height: 75%;
}
.detailed-purchase__price--inactive {
  color: var(--v-gray-700);
  text-decoration: line-through;
}
.detailed-purchase__price-container {
  line-height: 1;
}
.detailed-purchase__slogan {
  display: flex;
  align-items: center;
  gap: 0.3333rem;
}
.detailed-purchase__slogan-icon {
  font-size: 1.2rem;
  color: var(--v-yellow);
}
.detailed-purchase__buy {
  display: flex;
  flex-direction: column;
  gap: var(--v-detailed-purchase-buy-gap);
}
.detailed-purchase__btn {
  display: flex;
  gap: 0.6667rem;
  justify-content: center;
  align-items: center;
}
.detailed-purchase__btn:nth-child(2) {
  --v-btn-padding-y: 0.6667rem;
}
.detailed-purchase__actions {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  font-size: 0.7333rem;
}
.detailed-purchase__action {
  display: flex;
  align-items: center;
  gap: 0.6667rem;
  letter-spacing: 0.05em;
  cursor: pointer;
}
.detailed-purchase__icon {
  font-size: 1.3333rem;
}

.delivery__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1.15;
  padding-top: var(--v-detailed-purchase-delivery-item-padding);
  padding-bottom: var(--v-detailed-purchase-delivery-item-padding);
  border-bottom: 1px solid var(--v-gray-700);
}

@keyframes appear {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}